@use "global" as *;

.container {
    
}
.tab-main {


}
.tab-menu {

   
}

ul.tabs {
text-align: center;
    display: grid;  /*  */
    grid-template-columns: repeat(2fr, 1fr);  /* 31fr1 */
    gap: rem(32);  /* 10px */
    font-size: rem(14);
 
  @include mq("md") {
    grid-template-columns: repeat(4, 1fr);  /* 31fr1 */
    gap: rem(32);  /* 10px */
    font-size: rem(14);
    max-width: rem(874);
    }
  
}



.tabs__all {
    font-size: rem(14);
    border: rem(1) solid $green;
    padding: rem(6) rem(69);
    margin-right: rem(32);
    max-width: 162px;
    @include mq("md") {
        font-size: rem(14);
        border: rem(1) solid $green;
        padding: rem(6) rem(69);
        margin-right: rem(32);
    }
}

.tabs__text {
    font-size: rem(14);
    border: rem(1) solid $green;
    color: $green;
    padding: rem(6) rem(25);
margin-right: rem(32);
max-width: rem(176);
@include mq("md") {
    font-size: rem(14);
    border: rem(1) solid $green;
    color: $green;
    padding: rem(6) rem(25);
margin-right: rem(32);
}
}

.tabs__text:hover {

    color: $white;

}
.tabs .active .button2__item {
    background-color: $green; /*  */
    color: $white;           /*  */
    border: rem(1) solid $green; /*  */
}

.tabs .active .button2__item:hover {
    background-color: darken($green, 10%); /*  */
}
