@use "global" as *;

.button2 {}

.button2__items {
  display: block;
  grid-template-columns: repeat(2, 1fr);
  gap: rem(16);
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr)); /* 200px */
  justify-items: center;
  align-items: center;

  @include mq("md") {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(rem(1000), 1fr));
    grid-template-columns: repeat(4, 1fr);
    gap: rem(16) rem(32);
    display: inline;
  }
}

.button2__item {
  display: inline-block;
  width: 100%; //
  text-align: center; //
  position: relative;
  z-index: 1;
  background-color: $white; //
  padding-top: rem(4); //
  padding-left: rem(12); //
  padding-right: rem(12); //
  padding-bottom: rem(4); //
  border: rem(1) solid $green; //
  transition: background-color .3s;
}

.button2__item--green {
  background-color: $green; //
}

.button2__item--white {
  background-color: $white; //
}

.button2__item-link {
  font-size: rem(14); ///
  line-height: 1.5714; //
  color: $green; //
  font-family: $base-font-family;
  font-weight: $normal; //
  transition: color .3s; //

}

.button2__item-link--white {
  color: $white; //
}

.button2__item-link--green {
  color: $green; //

}

.button2__item::before {
  //.button
  background: $green; //
  position: absolute; //
  top: 0; ////
  left: 0; ////
  content: ''; //
  width: 100%; //(X)
  height: 100%; //(X)
  transform: scale(0, 1); //(X)with,height,transform: scale(0, 1);
  transform-origin: left top; //
  transition: .2s cubic-bezier(0.45, 0, 0.55, 1); //transition cubic-bezier:
  z-index: -1;
}

.button2__item--white::before {
  background-color: $white; //
}

.button2__item:hover .button2__item-link {
  //
  color: $white; //
}

.button2__item--white:hover .button2__item-link--green {
  color: $green; //
}

.button2__item:hover::before {
  transform: scale(1, 1); //buttonX
}

.button2__item--white {
  background-color: $white; //
}

.button2__item--white::before {
  background-color: $white; //
}

.button2__item--white:hover .button2__item-link--green {
  color: $green; //
}


